import React from 'react';
import ReactDOM from 'react-dom/client';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import './github-ms.css';
import 'highlight.js/styles/default.css'
import {marked} from 'marked';

const text =  `
# Welcome to my React Markdown Previewer!

## This is a sub-heading...
### And here's some other cool stuff:

Heres some code, \`<div></div>\`, between 2 backticks.

\`\`\`
// this is multi-line code:

function anotherExample(firstLine, lastLine) {
  if (firstLine == '\`\`\`' && lastLine == '\`\`\`') {
    return multiLineCode;
  }
}
\`\`\`

You can also make text **bold**... whoa!
Or _italic_.
Or... wait for it... **_both!_**
And feel free to go crazy ~~crossing stuff out~~.

There's also [links](https://www.freecodecamp.org), and
> Block Quotes!

And if you want to get really crazy, even tables:

Wild Header | Crazy Header | Another Header?
------------ | ------------- | -------------
Your content can | be here, and it | can be here....
And here. | Okay. | I think we get it.

- And of course there are lists.
  - Some are bulleted.
     - With different indentation levels.
        - That look like this.


1. And there are numbered lists too.
1. Use just 1s if you want!
1. And last but not least, let's not forget embedded images:

![freeCodeCamp Logo](https://cdn.freecodecamp.org/testable-projects-fcc/images/fcc_secondary.svg)
`;
let md = text.replace(String.fromCharCode(92), '');

class Markdown extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            content: md,
        }
        this.handleChange = this.handleChange.bind(this);
    };
    handleChange(event) {

        this.setState({
            content: event.target.value,
        })
    }
    render() {
        return (
            <div>
                <div className='editor-wrapper'>
                    <h4>Editor</h4>
                    <textarea id="editor" onChange={this.handleChange} value={this.state.content}/>
                </div>
                <div id="preview" className='markdown-wrapper markdown-body'>
                    <ReactMarkdown children={this.state.content} remarkPlugins={[remarkGfm]}/>
                </div>
            </div>
        )
    };
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(<Markdown />);

              
